import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import { IncomeInvoicesComponent } from './income-invoices.component';
import { IncomeViewInvoiceComponent } from './income-view-invoice/income-view-invoice.component';
import { IncomeViewInvoiceHistoryComponent } from './income-view-invoice-history/income-view-invoice-history.component';
import { IncomePostMultiInvoiceComponent } from './income-post-multi-invoice/income-post-multi-invoice.component';
import { IncomePayInvoiceComponent } from './income-pay-invoice/income-pay-invoice.component';

const routes: Routes = [
    {
		path     : '',
		component: IncomeInvoicesComponent,
		children : [
			{ path: '', redirectTo: 'all-invoices', pathMatch: 'full' },
            { path: 'all-invoices', component: IncomeViewInvoiceComponent },
            { path: 'view-invoice-history/:id', component: IncomeViewInvoiceHistoryComponent },
            { path: 'post-invoice', component: IncomePostMultiInvoiceComponent },
			{ path: 'post-multi-invoice/:id/:type', component: IncomePostMultiInvoiceComponent },
			{ path: 'edit-invoice/:id/:invoiceid', component: IncomePostMultiInvoiceComponent },
			{ path: 'pay-invoice/:id', component: IncomePayInvoiceComponent }
		]
	}
]

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class IncomeInvoicesRoutingModule { }